@import "./base/_mixins.sass";
@import "./base/_vars.sass";
.footer {
    background: $footer;
    height: 384px;
    padding-top: 120px;
    padding-bottom: 19px;
    font-family: "Montserrat", sans-serif;
    &__h4 {
        font-size: 18px;
        color: $colorTextContent;
        font-weight: 700;
        text-transform: uppercase;
        padding-bottom: 20px;
        letter-spacing: 1.5px; }
    &__contacts {
        float: left;
        width: 227px;
        margin-right: 65px;
        & > p:nth-child(2) {
            padding-bottom: 20px; }
        & > p {
            font-size: 14px;
            font-weight: 300;
            color: $dark; } }
    &__information {
        float: left;
        width: 430px;
        margin-right: 65px;
        & li {
            padding-bottom: 6px; }
        & li > a {
            font-size: 14px;
            color: $dark;
            font-weight: 700;
            text-transform: uppercase;
            transition: .2s;
            &:hover {
                color: $colorTextContent; } }
        & ul {
            display: inline-block; }
        & > ul:last-child {
            float: right; } }
    &__form {
        float: left;
        width: 374px;
        & > p {
            font-size: 14px;
            color: $dark;
            font-weight: 300;
            padding-bottom: 19px; }
        & > input {
            border: 1px solid $border;
            width: 368px;
            height: 46px;
            padding: 17px 19px 18px;
            outline: none;
            transition: .3s;
            &[placeholder] {
                font-size: 14px;
                color: $colorTextContent; }
            &:focus {
                color: $dark;
                background: $lightGrey; } } }
    &__copyright {
        font-size: 12px;
        color: $colorTextContent;
        font-weight: 700;
        text-transform: uppercase;
        clear: both;
        padding-top: 70px;
        & > p {
            display: inline-block; }
        & > p:last-child {
            float: right; } } }






